.practice-section {
    padding: 80px 0;
    font-family: 'Roboto'; 
    color: #ffffff;
    background-color: #021e43;
}

.practice-section h1{
    font-size: 3rem;
    text-align: center;
    font-weight: 100;
    padding-left: 3.5rem;
    font-family: "Libre Baskerville";
}

.about-practice{
    width: 90%;
    margin: 0 auto;
    font-weight: 100;
    font-size: 1rem;
    /*padding: 10px 0;*/
    margin-bottom: 1rem;
}

.practice-container {
    display: grid;
    z-index: 1;
    grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
    gap: 30px;
    max-width: 1200px;
    justify-content: center;
    margin: 0 auto;
    padding: 20px 20px;
    box-sizing: border-box;
    border-radius: 5px;
}
  

.practice-box {
    background-color: #ffffff;
    padding: 10px;
    text-align: center;
    z-index: 1;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.445);
    transition: transform 0.3s ease;
    border-bottom: 1px solid #333;
}

.practice-box:hover {
    transform: translateY(-5px);
}

.practice-icon {
    font-size: 2rem;
}

.practice-title {
    margin: 20px 0;
    color: #333;
}

.practice-description {
    color: #333;
    font-weight: 100;
    font-style: italic;
    text-align: center;
    text-transform: capitalize;
    font-size: 1rem;

}

@media (max-width: 768px) {
    .practice-section h1{
        font-size: 1.5rem;
        text-align: center;
        padding-right: 2.7rem;
    }
    
    .about-practice{
        width: 90%;
        margin: 0 auto;
        color: #ffffff;
        font-weight: 100;
        font-size: 1rem;
        padding: 20px 0;
    }

    .practice-icon{
        font-size: 2rem;
    }

    .practice-description{
        font-size: .8rem;
    }

    .practice-container {
        grid-template-columns: 1fr 1fr;
    }
}
