
.back-to-top-button {
    position: fixed;
    bottom: 20px;
    left: 20px;
    box-shadow: 2px 2px 10px #000000b6;
    color: #ffffff;
    border: none;
    border-radius: 50%;
    width: 50px;
    z-index: 10;
    height: 50px;
    font-size: 24px;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    opacity: 0;
    transition: all 0.3s ease;
  }
  
  .back-to-top-button.visible {
    opacity: 1;
  }
  
  .back-to-top-button:hover {
 
    box-shadow: 2px 2px 10px #000000d7;
    
  }
  