.about-section {
    padding: 50px 0;
    font-family: 'Roboto';
}

.about-content h2 span{
    color: #005377;
    font-size: 3.5rem;
    font-weight: 200;
    margin-bottom: 20px;
    font-family: "Libre Baskerville";
}


.about-content h2 {
    font-size: 2rem;
    color: #333;
    font-weight: 200;
    margin-bottom: 20px;
}


.about-container {
    width: 90%;
    margin: 0 auto;
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 10px;
    padding: 20px;
    box-sizing: border-box;
    position: relative;
}

.about-content {
    width: 47%;
    text-align: left;
}

.about-content p {
    font-size: 1rem;
    line-height: 1.5;
    font-style: italic;
    color: #333;
    margin-bottom: 20px;
}

.about-content .btn {
    display: inline-block;
    padding: 12px 25px;
    background-color: #005377;
    color: white;
    text-decoration: none;
    font-size: 1.1rem;
    border-radius: 20px;
    transition: all 0.5s ease;
}

.about-content .btn:hover {
    transform: translateY(-5px);
}

.about_images{
    width: 45%;
    height: 100vh;
    display: flex;
    padding-top: 8rem;
    justify-content: flex-end;
}

.about_image1{
    width: 30%;
    height: 30%;
    border-radius: 5%;
    background-color: #333;
    position: relative;
    bottom: 5rem;
    /*right: 15rem;*/
}

.about_image2{
    background-image: url(../image/header.jpg);
    width: 50vh;
    background-position: center;
    background-size: cover;
    height: 70vh;
    border-radius: 2%;
    position: absolute;
}

.about_image3{
    background-image: url(../image/litigation.jpg);
    width: 45%;
    height: 45%;
    border-radius: 2%;
    border: 10px solid #fff;
    background-position: center;
    background-size: cover;
    position: relative;
    top: 17rem;
    right: 15rem;
    z-index: 1;
}


@media (max-width: 768px) {
    .about-section{
        padding: 0;
    }
   .about-content h2 span{
      font-size: 1rem;
    }

   .about-content h2{
    font-size: 1.5rem;
   }     

    .about-container {
        flex-direction: column;
        gap: 40px;
    }

    .about-content {
        text-align: center;
        width: 100%;
    }

    .about-section span{
        font-size: 1.5rem;
    }

    .about_images{
        width: 90%;
    }

    .about_image1{
        width: 50%;
        height: 30%;
    }

    .about_image3{
        top: 25rem;
        right: 20rem;
    }
}

@media (max-width: 480px){

   .about_images{
       width: 100%;
   }

   .about_image1{
       width: 60%;
       height: 20%;
       border-radius: 5%;
       position: relative;
       bottom: 5rem;
       
   }
   
   .about_image2{
       width: 35vh;
       height: 55vh;
       border-radius: 2%;
       position: absolute;
       left: 3rem;
   }
   
   .about_image3{
       width: 70%;
       height: 25%;
       position: relative;
       top: 23rem;
       right: 10rem;
       z-index: 1;
   }
}