header{
    overflow: hidden;
    text-align: center;
    width: 100vw;
    height: 140vh;
    color: #ffff;
    position: relative;
    font-family: 'Roboto';
}

header::before{
    content: '';
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    right: 0;
    background-image: url('../image/mainhead.jpg');
    background-size: cover;
    background-position: center;
    /*filter: grayscale(100%);*/
    background-attachment: fixed;
    z-index: -1;
}

header h1{
    padding-top: 17%;
    font-size: 3.5rem;
    width: 70%;
    font-family: "Libre Baskerville", serif;
    font-weight: 200;
    font-style: normal;
    text-shadow: 1px 1px 4px black;
    margin: 0 auto;
}

.header_p{
    font-size: 1.5rem;
    width: 50%;
    margin: 0 auto;
    text-shadow: 1px 1px 4px black;
    color: #fff;
}

.box p{
  text-align: center;
  color: #333;
  width: 90%;
  font-size: 1rem;
}

header .btn-div{
  width: 15%;
  margin: 0 auto;
}

.btn {
    display: inline-block;
    padding: 12px 25px;
    background-color: #005377c0;
    color: white;
    text-decoration: none;
    font-size: 1.1rem;
    border-radius: 20px;
    transition: all 0.5s ease;
}

.clear{
    background-color: transparent;
    border: 1px solid #fff;
}

.btn:hover {
    background-color: #005377;
    border: none;
    transform: translateY(-5px);
}


.header_boxes{
    width: 90%;
    background-color: #fff;
    position: relative;
    top: 10%;
    padding: .3rem 0;
    box-shadow: 3px 3px 10px #808080af;
    border-radius: 2px;
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    margin: 0 auto;
    z-index: 1;
    box-shadow:  -3px -3px 10px #16161650, 3px 3px 10px #2c2c2c38;
}

.box{
    text-align: center;
    padding: 2.5rem .7rem;
    border-right: 1px solid #8080808a;
    transition: all 500ms ease;
    border-bottom: 3px solid #990f0200;
}

.box:hover{
    
    transform: translateY(-7px);
}

.box h2{
    color: #005377;
    font-size: 1.3rem;
    padding: 1rem 0;
    font-weight: 100;
    font-family: "Libre Baskerville";
}


.box img{
    width: 15%;
    padding-bottom: .8rem;
    margin: 0 auto;
}
@media (max-width: 768px) {
    header{
        height: 160vh;
    }

    header h1{
       width: 90%;
       padding-top: 70%;
       font-size: 2.5rem;
    }

     header .btn-div{
        width: 90%;
        gap: 1rem;
        justify-content: space-between;
        margin: 0 auto;
        flex-direction: row;
    } 

     header .btn{
        padding: 10px 30px;
        margin: 0 auto;
        font-size: 1rem;
     }

    header p{
        font-size: 1.5rem;
        width: 90%;
    }

     .header_boxes{
        grid-template-columns: repeat(2, 1fr);
    }
}

@media (max-width: 480px){
    header{
        height: 210vh;
    }
    .header_p{
        font-size: 1.2rem;
        width: 90%;
    }
    header h1{
    width: 90%;
    font-size: 2rem;
    }
    .header_boxes{
        position: relative;
        z-index: 1;
        grid-template-columns: repeat(1, 1fr);
        top: 8%;
        padding: 2.5rem 0;
    }
    .box{
        padding: 3rem 1rem;
        text-align: center;
    }
}