.newsletter-section {
    background: #333;
    padding: 25px 0;
    text-align: center;
    color: #fff;
    font-family: 'Roboto';
  }
  
  .newsletter-content {
    /*max-width: 600px;*/
    margin: 0 auto;
  }
  
  .newsletter-content h2 {
    color: #fff;
    font-size: 2rem;
    font-weight: 100;
    margin-bottom: 20px;
    font-family: "Libre Baskerville";
  }
  
  .newsletter-content p {
    font-size: 1rem;
    color: #dfdfdf;
    font-weight: 100;
    margin-bottom: 15px;
  }
  
  .newsletter-form {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 1rem;
  }
  
  .newsletter-form input[type="email"]{
    width: 300px;
    padding: 10px;
    font-size: 1rem;
    border: none;
    border-radius: 5px;
    outline: none;
    background-color: #ffffffaf;
    transition: background-color 0.3s ease;
    box-shadow: 2px 2px 10px rgba(0, 0, 0, 0.452);
  }
  
  input[type="email"]:focus {
    background-color: rgba(255, 255, 255, 0.89);
  }
  
  button {
    padding: 11px 20px;
    background-color: #021e43;
    color: #fff;
    border: none;
    border-radius: 5px;
    font-size: 1rem;
    cursor: pointer;
    transition: background-color 0.3s ease;
  }
  
  button:hover {
    background-color: #083145;
  }
  
  @media (max-width: 768px) {
     .newsletter-form{
        flex-direction: column;
     }

    .newsletter-content h2 {
      font-size: 1.5rem;
    }
  
   .newsletter-content p{
      font-size: 1.2rem;
      width: 80%;
      margin: 0 auto;
   }

   button{
    width: 92%;
   }

   .newsletter-form input[type="email"] {
      width: 300px;
      margin: 0 auto;
    }
  }
  
  @media (max-width: 480px) {
    .newsletter-content h2 {
      font-size: 2rem;
    }
  
    .newsletter-form input[type="email"] {
      width: 250px;
      font-size: 0.9rem;
    }
  
    button {
      padding: 12px 24px;
      font-size: 0.9rem;
    }
  }
  