footer{
    background-color: #021e43;
    font-family: 'Roboto';
}

.footer-top{
    display: flex;
    width: 95%;
    justify-content: space-between;
    align-items: center;
    margin: 0 auto;
    padding-top: 1rem;
}

.footer-icon{
    align-items: center;
    display: flex;
}

.footer-top h2{
    font-weight: 200;
    font-size: 2rem;
    font-family: "Libre Baskerville";
    color: #fff;
}

.footer-socials a{
    padding-left: 1rem;
}

.footer-content{
    display: grid;
    margin: 0 auto;
    width: 95%;
    grid-template-columns: repeat(4, 1fr);
    display: flex;
    justify-content: center;
    padding: 4rem 0;
    gap: 2rem;
}

.footer a{
    font-size: larger;
    font-weight: 200;
    text-decoration: none;
    cursor: pointer;
    text-align: center;
    color: #ffffffc5;
    transition: all .5s ease;
    border-bottom: 2px solid transparent;
}

.footer a:hover{
    transform: translateY(-10px);
    border-color: #fff;
}

.fawei_bottom-link{
    display: flex;
    justify-content: space-between;
    margin: 0 auto;
    padding: 1.5rem;
    width: 95%;
   }

.developers{
    padding: .5rem;
    text-align: center;
    width: 45%;
    margin: 0 auto;
}

.developers a{
    color: #f76d6d;
    font-weight: 100;
    font-size: 1rem;
}

.developers a span{
    color: #ffffff;
}

.copyrights{
    width: 45%;
    color: #fff;
    text-align: center;
    padding: .5rem;
}

.copyrights p{
    color: #fff;
}

@media only screen and (max-width: 600px){
    .footer-content{
       grid-template-columns: repeat(2, 1fr);
       gap: 1rem;
    }
    .footer-top{
        flex-direction: column;
        justify-content: flex-end;
        margin-top: 0;
    }
    .footer-icon{
        flex-direction: column;
    }

    .footer-icon h2{
       font-size: 1.3rem;
    }

    .footer-socials{
        margin-top: 2rem;
        margin-bottom: 2rem;
    }

    .fawei_bottom-link{
        flex-direction: column;
        justify-content: center;

    }

    .developers{
        width: 90%;
    }

    .copyrights{
        width: 90%;
    }
}