.contact-section{
    overflow: hidden;
    width: 100vw;
    padding: 3rem;
    color: #ffff;
    position: relative;
    font-family: 'Roboto';
} 

.contact-section::before{
    content: '';
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    right: 0;
    background-image: url('../image/litigation.jpg');
    background-size: cover;
    background-position: center;
    background-attachment: fixed;
    filter: grayscale(100%);
    text-align: center;
    z-index: -1;
}

.form_div{
  display: flex;
  width: 65%;
  margin: 0 auto;
  background-color: #ffff;
  justify-content: space-between;
  padding: 2rem;
}

.contact-form {
   width: 60%;
}

.contact-form h2{
  color: #005377;
  font-weight: 100;
  font-size: 2rem;
}

.contact-form p{
  font-weight: 100;
  color: #333;
  padding: .5rem 0;
}
  
  .form-group {
    margin-bottom: 10px;
  }

  .form-group input[type="text"],
  input[type="tel"],
  input[type="email"],
  textarea {
    width: 100%;
    padding: 10px;
    font-size: 1rem;
    border: none;
    outline: none;
    background-color: #f3f3f3e3;
    transition: background-color 0.3s ease;
  }  

input[type="text"]:focus,
input[type="tel"]:focus,
input[type="email"]:focus,
textarea:focus {
  border-bottom: 1px solid #333333;
  border: none;
}

input[type="text"]::placeholder,
input[type="tel"]::placeholder,
input[type="email"]::placeholder,
textarea::placeholder {
  color: #333;
  font-weight: 100;
  font-size: .9rem;
}
  
textarea {
    resize: vertical;
}
  
.bttn {
  display: inline-block;
  padding: 12px 25px;
  background-color: #005377;
  color: white;
  text-decoration: none;
  font-size: 1.1rem;
  border-radius: 20px;
  transition: all 0.5s ease;
  }
  
  .bttn:hover {
    transform: translateY(-10px);
  }

  .contact_extra-info{
    width: 40%;
    background-color: #005377;
    z-index: 10;
    padding: 1rem;
    box-shadow: 2px 2px 10px #0000007a;
    position: relative;
    right: 5rem;
  }

  .contact_extra-info h2{
    font-weight: 1;
    color: #ffff;
    margin-left: 1.5rem;
  }
  .extra_info{
    padding: 1rem;
  }
  .extra_info h4{
    color: #ffff;
  }

  .text{
    width: 100%;
    display: flex;
    gap: 1rem;
    align-items: center;
    margin-top: 35px;
  }

  .text img{
    width: 10%;
  }

  .text h4{
    font-weight: 100;
    font-size: 1rem;
  }
  
  @media (max-width: 768px) {
    .contact-form {
      padding: 2px;
    }
    
    .contact-h1{
      font-size: 1.5rem;
    }

    .bttn {
      padding: 10px 20px;
    }
    .form_div{
      width: 90%;
    }
    .contact_extra-info{
      width: 55%;
    }
}

@media (max-width: 480px){
  .form_div{
    flex-direction: column;
    width: 110%;
  }

  .contact_extra-info{
    width: 120%;
    
  }

  .contact-form{
    width: 100%;
  }
}
  