*{
    padding: 0;
    box-sizing: border-box;
    margin: 0;
    text-decoration: none;
    list-style: none;
    scroll-behavior: smooth;
  }

body{
    scroll-behavior: smooth;
    overflow-x: hidden;
}

::-webkit-scrollbar{
    width: 7px;
}

::-webkit-scrollbar-thumb{
    background: #021e43;
    border-radius: 20px;
}


.reveal{
    position: relative;
    transform: translateY(150px);
    opacity: 0;
    transition: all 1s ease;
  }
  
  .reveal.active{
    transform: translateY(0px);
    opacity: 1;
  }
  
  .revealtwo{
    position: relative;
    transform: translateX(150px);
    opacity: 0;
    transition: all 1s ease;
  }
  
  .revealtwo.activetwo{
    transform: translateX(0px);
    opacity: 1;
  }
  
  @media (max-width: 768px){
    html{
      width: 100%;
      height: 100%;
      margin: 0;
      padding: 0;
      overflow-x: hidden;
    }
 
  }