.attorneys-section{
    padding: 2rem;
    display: flex;
    flex-direction: column;
    font-family: 'Roboto';
    text-align: center;
}

.attorneys-section a{
    color: #fff;
    font-weight: 500;
}

.head-text{
    text-align: center;
    font-size: 3rem;
    font-weight: 200;
    padding: 1rem;
    color: #004563;
    font-family: "Libre Baskerville";
}

.attorney-1{
    width: 100%;
    display: flex;
    justify-content: space-between;
    margin: 0 auto;
    align-items: center;
    gap: 2rem;
    padding: 1.5rem 0 1.5rem 0;
}

.attorney_div{
    display: flex;
    justify-content: space-between;
    width: 100%;
}


.attorney{
    width: 25%;
    display: flex;
    margin: 0 auto;
    flex-direction: column;
    justify-content: center;
    gap: 2rem;
    padding: 1.5rem 0 1.5rem 0;
}

.profile-2 {
    width: 65%;
    margin: 0 auto;
}

.profile-image {
    flex: 1;
    text-align: center;
    position: relative;
    overflow: hidden;
}

.profile-image_principle {
    flex: 1;
    text-align: center;
    position: relative;
    overflow: hidden;
}

.profile-image::after {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 1;
    opacity: 0.9;
}

.profile-image_principle::after {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 1;
    opacity: 0.9;
}

.profile-image img {
    max-width: 80%;
    height: auto;
    border-radius: 10px;
    box-shadow: 10px 15px 1px #8080805b;
    z-index: 2;
    position: relative;
    transition: transform 0.3s ease;
}

.profile-image_principle img {
    max-width: 80%;
    height: auto;
    border-radius: 10px;
    box-shadow: 10px 15px 1px #8080805b;
    z-index: 2;
    position: relative;
    transition: transform 0.3s ease;
}

.profile-image:hover img {
    transform: scale(1.05); 
}

.profile-image_principle:hover img {
    transform: scale(1.05); 
}

.content_principle{
    width: 70%;
}

.content_principle p{
    font-weight: 100;
    font-style: italic;
    font-size: 1rem;
    color: #252525;
}

.clr-bg_principle{
    background-color: #021e43;
    box-shadow: 4px 4px 8px #555555a1;
    border-radius: 5px;
    text-align: center;
    padding: .7rem;
    color: #ffffff;
    font-weight: 100;
}

.clr-bg_principle h2{
   font-weight: 100;
}

.other_lawyers-text{
    font-size: 3rem;
    font-weight: 100;
    color: #333;
    font-family: "Libre Baskerville";
}

.clr-bg{
    background-color: #021e43;
    box-shadow: 4px 4px 8px #555555a1;
    border-radius: 5px;
    text-align: center;
    padding: .7rem;
}

.clr-bg h2{
    color: #fff;
    font-weight: 100;
    font-size: 1rem;
}

.clr-bg h3{
    color: #ffffff;
    font-weight: 100;
    font-size: .8rem;
}

.content p{
    font-size: 1rem;
    font-weight: 100;
    color: #333;
    font-style: italic;
}

.contact-info_principle{
    display: inline-block;
    padding: 12px 25px;
    background-color: #005377;
    color: white;
    text-decoration: none;
    font-size: 1.1rem;
    margin-top: 1rem;
    border-radius: 20px;
    transition: all 0.5s ease;
}

.contact-info{
    display: inline-block;
    padding: 12px 25px;
    background-color: #005377;
    color: white;
    text-decoration: none;
    font-size: 1.1rem;
    margin-top: 1rem;
    border-radius: 20px;
    transition: all 0.5s ease;
}

.contact-info:hover{
    transform: translateY(-10px);
}

.profile-1{
    width: 100%;
}

h4{
    color: #003170;
    font-weight: 500;
}

.profile-2 img {
    width: 100%;
    height: auto;
    padding-bottom: .5rem;
    border-radius: 10px;
    z-index: 2;
    position: relative;
    transition: transform 0.3s ease;
    /*box-shadow: 10px 15px 1px #8080805b;*/
}

.profile-2:hover img {
    transform: scale(1.05); 
}


.slick-slide > div{
    margin: 0 2rem;
}

@media (max-width: 768px) {
    .attorney-1{
        flex-direction: column;
    }
    .head-text{
        font-size: 1.5rem;
        font-weight: 300;
    }
    .content{
        width: 95%;
        text-align: center;
    }
    .contact-info{
        flex-direction: column;
    }
    p{
        font-size: .9rem;
    }
    .attorney-2{
        flex-direction: column;
    }
    .profile-1{
        width: 95%;
    }
    .profile-2{
        width: 95%;
    }
    .profile-3{
        width: 95%;
    }

    .clr-bg h2{
        font-size: 1rem;
    }

    .clr-bg h3{
        font-size: 1rem;
        font-weight: 100;
    }
}

@media (max-width: 480px){
    .clr-bg_principle{
        width: 100%;
    }
    .content_principle{
        width: 90%;
    }
    .clr-bg_principle h2{
        font-size: 1.3rem;
    }
}

