.alert {
    position: fixed;
    top: 20px;
    right: 20px;
    background-color: rgb(183, 255, 214);
    color: seagreen;
    border-radius: 5px;
    padding: 15px;
    display: flex;
    align-items: center;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
    z-index: 1000;
    max-width: 300px;
    width: 80%;
  }
  
  .alert-message {
    flex: 1;
    font-size: 16px;
  }
  
  .alert-close {
    background: none;
    border: none;
    color: #721c24;
    font-size: 20px;
    cursor: pointer;
    padding: 0;
    margin-left: 15px;
    transition: color 0.3s ease;
  }
  
  
  .alert-close:hover {
    color: seagreen;
  }

  .alert-close:hover {
    color: seagreen;
  }
  